<template>
  <img
    v-bind:alt="src"
    v-bind:src="src"
    v-on:error="base64"
    class="icon"
  />
</template>

<script>
import axios from "axios";
const JSONbig = require("json-bigint");
axios.defaults.transformResponse = [
  function(data) {
    // Do whatever you want to transform the data
    return JSONbig.parse(data);
  }
];

export default {
  name: "icon",
  props: ["src"],
  methods: {
    base64: function() {
      axios
        .get("/HisdMskz/getImage", {
          params: {
            url: this.src
          }
        })
        .then(response => (this.src = response.data.base64));
    }
  }
};
</script>
<style>

</style>


