<template>
  <div class="container-fluid">
    <div class="media">
      <div class="media-left mr-1">
        <Icon class="icon" alt v-bind:src="user.profileImageURL" />
      </div>
      <div class="media-body d-flex align-items-center">
        <div v-text="user.screenName" style="white-space:pre-wrap; word-wrap:break-word;"></div>
        <div v-text="user.description">
        </div>
      </div>
      <div>
        <button
          class="btn btn-danger btn-lg"
          v-on:click="block()"
          v-bind:disabled="blocked"
        >ブロック</button>
      </div>
    </div>
  </div>
</template>

<script>
import Icon from "./Icon";
import axios from "axios";
const JSONbig = require("json-bigint");
axios.defaults.transformResponse = [
  function(data) {
    // Do whatever you want to transform the data
    return JSONbig.parse(data);
  }
];

export default {
  name: "User",
  props: ["user"],
  methods: {
    block: function() {
      console.log(this.user);
      this.blocked = true
      axios
        .get("../block",{ params: {target: BigInt(this.user.id) }})
        .then(
          response => (
            (this.user = response.data.resurlt)
          )
        )
        .catch(
          e => console.log(e),
          this.blocked = false
        );
    }
  },
  mounted() {},
  data() {
    blocked :false
  },
  components: { Icon }
};
</script>
<style>
</style>


