<template>
  <div id="app">
    <div class="card border-primary">
      <div v-if="listUser.length  > 0">
        <ul class="list-group list-group-flush">
          <li class="list-group-item" v-for="item in listUser" v-bind:key="item.id">
            <User v-bind:user="item" />
          </li>
        </ul>
      </div>
      <div class="card-footer">
        <button
          class="btn btn-primary btn-lg btn-block"
          v-on:click="addFollower()"
          v-bind:disabled="cursor == 0"
        >Followerを更に表示</button>
      </div>
    </div>
  </div>
</template>

<script>
import User from "@/components/User";
import axios from "axios";
const JSONbig = require("json-bigint");
axios.defaults.transformResponse = [
  function(data) {
    // Do whatever you want to transform the data
    return JSONbig.parse(data);
  }
];
export default {
  name: "App",
  components: { User },
  created() {
    this.getFollower();
  },
  methods: {
    getFollower: function() {
      console.log(this);
      axios
        .get("../getFollower")
        .then(
          response => (
            (console.log(response)),
            (this.listUser = response.data.users),
            (this.cursor = response.data.cursor),
            (this.rateLimitStatus = response.data.rateLimitStatus)
          )
        ).catch(e => { console.log(e); });
    },
    addFollower: function() {
      console.log(this);
      axios
        .get("../getFollower", { params: { cursor: BigInt(this.cursor) } })
        .then(
          response => (
            (this.listUser = this.listUser.concat(response.data.users)),
            (this.cursor = response.data.cursor)(
              (this.rateLimitStatus = response.data.rateLimitStatus)
            )
          )
        );
    }
  },
  data() {
    return {
      listUser: [],
      cursor: -1,
      rateLimitStatus: Object
    };
  }
};
</script>

<style>
</style>


